body {
  background-color: #0f0e13;
  color: #e0e0e0;
  font-family: 'Roboto', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  margin: 0;
  padding: 0;
}

.App {
  max-width: 1200px;
  margin: 0 auto;
  padding: 40px 20px;
  line-height: 1.6;
}

.App-header {
  text-align: center;
  margin-bottom: 60px;
}

h1 {
  color: #8a2be2;
  font-size: 3.5em;
  margin-bottom: 10px;
  text-shadow: 0 0 10px rgba(138, 43, 226, 0.5);
}

.tagline {
  font-style: italic;
  color: #bbb;
  font-size: 1.2em;
  margin-bottom: 30px;
}

.employee-verification, .sign-in-container, .generate-qr-container {
  background-color: #1a1a2e;
  padding: 30px;
  border-radius: 15px;
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(4px);
  border: 1px solid rgba(255, 255, 255, 0.18);
  margin-bottom: 30px;
}

.form-group {
  margin-bottom: 20px;
}

label {
  display: block;
  margin-bottom: 5px;
  color: #8a2be2;
}

input {
  width: 100%;
  padding: 12px;
  border: 1px solid #4a4a6a;
  border-radius: 8px;
  background-color: #2a2a3a;
  color: #e0e0e0;
  font-size: 1em;
  transition: all 0.3s ease;
}

input:focus {
  border-color: #8a2be2;
  box-shadow: 0 0 0 2px rgba(138, 43, 226, 0.2);
  outline: none;
}

.btn {
  padding: 12px 24px;
  font-size: 1em;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
  text-align: center;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.btn-primary {
  background-color: #8a2be2;
  color: #ffffff;
}

.btn-secondary {
  background-color: transparent;
  color: #8a2be2;
  border: 2px solid #8a2be2;
}

.btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(138, 43, 226, 0.4);
}

.btn-primary:hover {
  background-color: #9b4deb;
}

.btn-secondary:hover {
  background-color: rgba(138, 43, 226, 0.1);
}

.button-container {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-top: 30px;
}

.result-container {
  margin-top: 20px;
  background-color: #2a2a3a;
  padding: 20px;
  border-radius: 8px;
}

.result-container h3 {
  color: #8a2be2;
  margin-bottom: 15px;
}

.qr-code {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.wallet-adapter-button-trigger {
  background-color: #8a2be2 !important;
  color: white !important;
  padding: 12px 24px !important;
  border-radius: 8px !important;
  font-weight: bold !important;
  text-transform: uppercase !important;
  letter-spacing: 1px !important;
  transition: all 0.3s ease !important;
}

.wallet-adapter-button-trigger:hover {
  background-color: #9b4deb !important;
  transform: translateY(-2px) !important;
  box-shadow: 0 5px 15px rgba(138, 43, 226, 0.4) !important;
}

@media (max-width: 768px) {
  .wallet-adapter-button-trigger {
    width: 100% !important;
    margin-bottom: 10px !important;
  }
}